html {
  box-sizing: border-box;
  font-size: 100%; }

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none; }

h1 {
  margin: 0;
  padding: 0; }

a {
  display: block; }

img {
  display: block;
  max-width: 100%; }

body {
  font-family: sans-serif;
  color: #fff; }

.page {
  background: url("../../images/bg-dark.jpg") no-repeat center center;
  background-size: cover;
  min-height: 600px; }
  @media (min-width: 64em) {
    .page {
      height: 100vh; } }
  .page__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    max-width: 75rem;
    margin-left: auto;
    margin-right: auto;
    padding-left: 0.9375rem;
    padding-right: 0.9375rem; }

.header {
  max-width: 75rem;
  margin-left: auto;
  margin-right: auto;
  padding: 2.1875rem 0.9375rem 8.125rem 0.9375rem;
  flex: 1 1 100%; }
  @media (max-width: 63.9375em) {
    .header {
      padding-bottom: 4.0625rem; } }

.main {
  flex: 0 0 100%; }
  @media (min-width: 64em) {
    .main {
      max-width: 39.375rem; } }
  .main__title {
    font-size: 1.875rem;
    line-height: 1.4; }
    @media (max-width: 37.4375em) {
      .main__title {
        max-width: 25rem;
        margin: 0 auto 0.625rem auto; } }
    @media (min-width: 37.5em) {
      .main__title {
        font-size: 2.125rem; } }
  .main__text {
    font-size: 1.125rem;
    line-height: 1.2;
    margin-bottom: 2.8125rem; }
    @media (max-width: 37.4375em) {
      .main__text {
        max-width: 25rem;
        margin: 0 auto 2.8125rem auto; } }
    @media (min-width: 37.5em) {
      .main__text {
        font-size: 1.5rem; } }
  .main__list {
    display: flex;
    flex-wrap: wrap; }
  .main__item {
    flex: 0 0 300px;
    display: flex;
    align-items: center; }
    @media (min-width: 37.5em) {
      .main__item {
        flex: 0 0 50%;
        max-width: 50%; } }
    @media (max-width: 37.4375em) {
      .main__item {
        margin: 0 auto; } }
    @media (max-width: 37.4375em) {
      .main__item:not(:last-child) {
        margin-bottom: 1.875rem; } }
    @media (min-width: 37.5em) {
      .main__item:nth-child(-n+2) {
        margin-bottom: 1.875rem; } }

.item__number {
  display: block;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  border: 2px solid #fff;
  border-radius: 50%;
  margin-right: 0.9375rem;
  flex: 0 0 50px; }

.item__text {
  flex: 1; }
  @media (min-width: 37.5em) {
    .item__text {
      padding-right: 5.3125rem; } }

.call {
  width: 300px; }
  @media (max-width: 63.9375em) {
    .call {
      margin: 0 auto;
      padding-top: 4.0625rem;
      padding-bottom: 2.8125rem; } }
  @media (max-width: 37.4375em) {
    .call {
      flex: 1; } }

.form * {
  display: block;
  width: 100%; }

.form__input-box {
  position: relative; }

.form__label {
  font-size: 1.125rem;
  color: #000;
  position: absolute;
  top: 14px;
  left: 15px;
  cursor: text;
  transition: .2s linear; }
  .form__label.active {
    font-size: 0.625rem;
    top: 2px; }

.form__input {
  padding: 0.9375rem 1rem;
  background-color: rgba(255, 255, 255, 0.8);
  border: 0;
  outline: 0;
  margin-bottom: 1.25rem; }
  .form__input:focus {
    border: 2px solid #f6871e; }
    .form__input:focus + .form__label {
      font-size: 0.625rem;
      top: 2px; }

.form__button {
  background-color: #f6871e;
  border: 0;
  outline: 0;
  cursor: pointer;
  padding: 0.9375rem 0rem;
  color: #fff;
  text-transform: uppercase;
  font-weight: bold; }

.form__error-message {
  position: absolute;
  bottom: -15px;
  font-size: 0.625rem;
  font-weight: bold;
  color: #000;
  display: none;
  padding: 0.3125rem;
  background-color: #e76262; }
  .form__error-message.active {
    display: block; }
